<template>
  <div class="container">
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="学科名称">
        <el-input
          v-model="form.subjectName"
          style="width: 192px"
          size="small"
        ></el-input>
        <el-button size="small" style="margin-left: 10px" @click="clear"
          >清除</el-button
        >
        <el-button type="primary" size="small" @click="submit">搜索</el-button>
        <el-button
          type="success"
          icon="el-icon-edit"
          size="small"
          style="float: right"
          @click="add('add')"
          >新增学科</el-button
        >
      </el-form-item>
    </el-form>
    <el-alert
      :title="`数据一共${count}条`"
      type="info"
      :closable="false"
      show-icon
      style="margin-bottom: 15px"
    >
    </el-alert>
    <el-table
      :data="tableData"
      style="width: 100%"
      :header-cell-style="getRowClass"
    >
      <el-table-column type="index" label="序号" width="48"> </el-table-column>
      <el-table-column prop="subjectName" label="学科名称"> </el-table-column>
      <el-table-column prop="username" label="创建者"> </el-table-column>
      <el-table-column prop="addDate" label="创建日期" width="160">
        <template slot-scope="{ row }">
          {{ row.addDate | parseTimeByString }}
        </template>
      </el-table-column>
      <el-table-column prop="isFrontDisplay" label="前台是否显示">
        <template slot-scope="{ row }">
          {{ row.isFrontDisplay === 0 ? "否" : "是" }}
        </template>
      </el-table-column>
      <el-table-column prop="twoLevelDirectory" label="二级目录">
      </el-table-column>
      <el-table-column prop="tags" label="标签"> </el-table-column>
      <el-table-column prop="totals" label="题目数量"> </el-table-column>
      <el-table-column prop="address" label="操作" width="240">
        <template slot-scope="{ row }">
          <el-button
            type="text"
            @click="
              $router.push(`directorys?id=${row.id}&name=${row.subjectName}`)
            "
            >学科分类</el-button
          >
          <el-button
            type="text"
            @click="$router.push(`tags?id=${row.id}&name=${row.subjectName}`)"
            >学科标签</el-button
          >
          <el-button type="text" @click="add('edit', row.id)">修改</el-button>
          <el-button type="text" @click="del(row.id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <el-pagination
      style="text-align: right; margin-top: 20px"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :page-sizes="[5, 10, 20, 50]"
      :page-size="10"
      background
      layout=" prev, pager, next,sizes, jumper"
      :total="count"
      hide-on-single-page
    >
    </el-pagination>
    <!-- 弹窗 -->
    <el-dialog
      :title="title1"
      :visible.sync="dialogVisible"
      :before-close="handleClose"
      width="30%"
    >
      <el-form ref="form" label-width="80px" :model="formdata" :rules="rules">
        <el-form-item label="学科名称" prop="subjectName">
          <el-input
            placeholder="请输入学科名称"
            style="width: 280px"
            v-model="formdata.subjectName"
          ></el-input>
        </el-form-item>
        <el-form-item label="是否显示">
          <el-switch
            active-color="#13ce66"
            inactive-color="#ff4949"
            v-model="formdata.isFrontDisplay"
          ></el-switch>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="clear1">取 消</el-button>
        <el-button type="primary" @click="determine">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { list, add, detail, update, remove } from '@/api/hmmm/subjects'
export default {
  data () {
    return {
      form: {
        page: 1,
        pagesize: 10,
        subjectName: ''
      },
      formdata: {
        subjectName: '',
        isFrontDisplay: true,
        id: 0
      },
      count: 13,
      tableData: [],
      dialogVisible: false,
      type: 'add',
      rules: {
        subjectName: [
          { required: true, message: '请输入学科名称', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getList()
  },
  computed: {
    title1 () {
      const title = ''
      if (this.type === 'add') return '新增学科'
      if (this.type === 'edit') return '修改学科'
      return title
    }
  },
  methods: {
    // 改变表头样式
    getRowClass ({ rowIndex, columnIndex }) {
      if (rowIndex === 0) {
        return 'background:#f8faff; border-bottom: 2px solid #e8e8e8;'
      }
    },
    // 获取数据
    async getList () {
      const res = await list(this.form)
      // console.log(res)
      this.count = res.data.counts
      this.tableData = res.data.items
    },
    // 重置
    clear () {
      this.form.subjectName = ''
      this.getList()
    },
    // 搜索
    submit () {
      this.getList()
    },
    async add (type, id) {
      // console.log(type)
      this.dialogVisible = true
      this.type = type
      // console.log(id)
      this.formdata.id = id
      if (type === 'edit') {
        const res = await detail({ id })
        // console.log(res)
        this.formdata.subjectName = res.data.subjectName
        this.formdata.isFrontDisplay = res.data.isFrontDisplay === 1
      }
    },
    async determine () {
      await this.$refs.form.validate()
      if (this.type === 'add') {
        await add(this.formdata)
        this.$message.success('新增成功')
      } else {
        await update(this.formdata)
        // console.log(res)
        this.$message.success('修改成功')
      }
      this.getList()
      this.clear1()
    },
    clear1 () {
      this.dialogVisible = false
      this.formdata.subjectName = ''
    },
    handleClose () {
      this.clear1()
    },
    async del (id) {
      const res = await this.$confirm(
        '此操作将永久删除该文件, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(() => {})
      // console.log(res)
      if (res === 'confirm') {
        const data = await remove({ id })
        // console.log(data)
        if (data.data.success) {
          this.getList()
          this.$message.success('删除成功')
        } else {
          this.$message.error(data.data.error)
        }
      }
    },

    handleSizeChange (val) {
      // console.log(`每页 ${val} 条`)
      this.form.pagesize = val
      this.getList()
    },
    handleCurrentChange (val) {
      // console.log(`当前页: ${val}`)
      this.form.page = val
      this.getList()
    }
  }
}
</script>

<style scoped>
.container {
  padding: 20px;
  margin: 10px;
  background-color: #fff;
}
.el-pagination button,
.el-pagination span:not([class*="suffix"]) {
  margin: 0 !important;
}
</style>
